import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';
import { ApplyLeaveComponent } from './apply-leave/apply-leave.component';
import { ListLeaveComponent } from './list-leave/list-leave.component';
import { ReviewLeaveComponent } from './review-leave/review-leave.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Leave'
    },
    children: [
      {
        path: '',
        redirectTo: 'list'
      },
      {
        path: 'list',
        data: {
          title: 'List',
          access: {
            module: 'leave',
            entity: 'leave',
            action: 'find'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ListLeaveComponent
      },
      {
        path: 'review',
        data: {
          title: 'Review',
          access: {
            module: 'leave',
            entity: 'leave',
            action: 'review'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ReviewLeaveComponent
      },
      {
        path: 'apply',
        data: {
          title: 'Apply',
          access: {
            module: 'leave',
            entity: 'leave',
            action: 'create'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ApplyLeaveComponent
      },
      {
        path: 'policy',
        loadChildren: './policy/leave-policy.module#LeavePolicyModule'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LeaveRoutingModule { }
