import { Injectable } from '@angular/core';

import { TYPE } from 'config/constants/leave';
import { Api } from 'core/services/api.service';

class ReviewedBy {

  user_id: string;
  name: string
};

export class ApplyLeave {

  leave_type: TYPE;
  start_date: string = '';
  end_date: string = '';
  summary: string = '';
};

export class Leave {

  _id: string;
  user_id: string;
  name: string;
  leave_type: string;
  start_date: string;
  end_date: string;
  leave_count: number;
  summary: string;
  reviewed_by: ReviewedBy;
  leave_status: number;
};

@Injectable()
export class LeaveService extends Api {

  private _base_url = '/leaves';

  apply(leave: ApplyLeave): Promise<Object> {

    return this.post(this._base_url, leave)
      .then(response => response);
  }

  getLeaves(query?: Map<string, any>): Promise<Leave[]> {

    return this.get(this._base_url, query)
      .then(response => response['data'] as Leave[]);
  }

  getReviewLeaves(query?: Map<string, any>): Promise<Leave[]> {

    return this.get(this._base_url + '?review=true', query)
      .then(response => response['data'] as Leave[]);
  }

  approve(leave_id: string): Promise<Object> {

    return this.patch(this._base_url + '/review/' + leave_id, {})
      .then(response => response);
  }

  reject(leave_id: string): Promise<Object> {

    return this.delete(this._base_url + '/review/' + leave_id, {})
      .then(response => response);
  }

  cancel(leave_id: string): Promise<Object> {

    return this.delete(this._base_url + '/' + leave_id)
      .then(response => response);
  }
}
