import { Component, OnInit, ViewEncapsulation } from '@angular/core';

import { Leave, LeaveService } from 'modules/leave/leave.service';
import { STATUS } from 'config/constants/leave';
import { Access } from 'core/providers/access.service';

@Component({
  selector: 'app-list-leave',
  templateUrl: './list-leave.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [LeaveService]
})
export class ListLeaveComponent implements OnInit {

  leaves: Leave[] = [];

  loading: boolean = false;
  applyAllowed: boolean = false;
  reviewAllowed: boolean = false;

  cancelling: Array<Boolean>;

  constructor(
    private leaveService: LeaveService,
    private access: Access
  ) {

    this.cancelling = new Array<Boolean>();
  }

  async ngOnInit() {

    this.applyAllowed = await this.access.hasAccess({
      module: 'leave',
      entity: 'leave',
      action: 'create'
    });

    this.reviewAllowed = await this.access.hasAccess({
      module: 'leave',
      entity: 'leave',
      action: 'review'
    });

    this.loadLeaves();
  }

  loadLeaves() {

    const self = this;
    this.loading = true;

    this.leaveService.getLeaves().then(leaves => {

      self.loading = false;
      this.leaves = leaves;
    });
  }

  cancelLeave(index) {

    const leave_id = this.leaves[index]._id;
    const self = this;

    this.cancelling[index] = true;
    this.leaveService.cancel(leave_id).then(response => {

      self.leaves[index].leave_status = STATUS.DELETED;
      self.cancelling[index] = false;
    });
  }

  getStatusBadge(status) {

    switch (status) {

      case 2: return 'badge-success';
      case 3: return 'badge-danger';
      case 4: return 'badge-dark';
      default: return 'badge-default';
    }
  }
}
