import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { DatePipe } from '@angular/common';

import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';

import { LeavePolicyService, LeavePolicy } from 'modules/leave/policy/leave-policy.service';
import { ApplyLeave, LeaveService } from 'modules/leave/leave.service';
import { ProfileService } from 'modules/user/profile/profile.service';
import { BsDatepickerDirective } from 'ngx-bootstrap';

const error_map = {
  start_date: 'Start Date',
  end_date: 'End Date',
  leave_type: 'Leave Type',
  summary: 'Summary'
};

@Component({
  selector: 'app-apply-leave',
  templateUrl: './apply-leave.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [LeaveService, LeavePolicyService, DatePipe]
})
export class ApplyLeaveComponent implements OnInit {
  @ViewChild('errorModal') errorModal: ErrorModalComponent;
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('startDate') startDate: BsDatepickerDirective;
  @ViewChild('endDate') endDate: BsDatepickerDirective;

  leave: ApplyLeave;
  leave_type_value: Object;

  sdConfig: Object;
  edConfig: Object;

  error: string;
  invalid_attributes: string[];

  leave_type: Object = { };

  constructor(
    private leavePolicyService: LeavePolicyService,
    private leaveService: LeaveService,
    private profileService: ProfileService,
    private datePipe: DatePipe
  ) {

    this.leave = new ApplyLeave();

    this.sdConfig = {
      dateInputFormat: 'YYYY-MM-DD',
      containerClass: 'theme-blue',
      showWeekNumbers: false
    };
    this.edConfig = {
      dateInputFormat: 'YYYY-MM-DD',
      containerClass: 'theme-blue',
      showWeekNumbers: false
    };
  }

  private __validate() {
    return true;
  }

  private __sanitizeLeave(leave) {

    if (leave.start_date !== undefined) {
      leave.start_date = this.datePipe.transform(leave.start_date, 'yyyy-MM-dd');
    }

    if (leave.end_date !== undefined) {
      leave.end_date = this.datePipe.transform(leave.end_date, 'yyyy-MM-dd');
    }

    return leave;
  }

  async ngOnInit() {

    this.profileService.getProfile('leaves')
      .then(profile => {

        const leave_types: Object = {};
        if (profile.leaves) {
          profile.leaves.forEach(function (leave) {
            if (leave.leaves > 0) {
              leave_types[leave.leave_type] = leave.leave_type;
            }
          });
        }
        this.leave_type = leave_types;

      }).catch(err => {

        this.error = err;
        this.errorModal.show();
      });
  }

  setStartDate(value: Date) {

    this.edConfig['minDate'] = value;
    this.endDate.setConfig();
  }

  applyLeave() {

    if (this.__validate()) {

      let leave = JSON.parse(JSON.stringify(this.leave));
      leave = this.__sanitizeLeave(leave);
      this.error = '';
      this.invalid_attributes = [];

      this.leaveService.apply(leave).then(data => {

        this.successModal.show('/leave/list');

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.invalid_attributes) {
          for (const key in err.error.invalid_attributes) {
            if (err.error.invalid_attributes.hasOwnProperty(key)) {
              this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
            }
          }
        }
        this.errorModal.show();
      });
    }
  }
}
