import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { Leave, LeaveService } from 'modules/leave/leave.service';
import { STATUS } from 'config/constants/leave';
import { Access } from 'core/providers/access.service';

@Component({
  selector: 'app-review-leave',
  templateUrl: './review-leave.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [LeaveService]
})
export class ReviewLeaveComponent implements OnInit {

  leaves: Leave[] = [];

  reviewed: Array<Boolean>;
  approving: Array<Boolean>;
  rejecting: Array<Boolean>;
  loading: boolean = false;

  approveAllowed: boolean;
  rejectAllowed: boolean;

  constructor(
    private leaveService: LeaveService,
    private access: Access
  ) {

    this.reviewed = [];
    this.approving = [];
    this.rejecting = []
  }

  async ngOnInit() {

    this.loadLeaves();

    this.approveAllowed = await this.access.hasAccess({
      module: 'leave',
      entity: 'review',
      action: 'approve'
    });

    this.rejectAllowed = await this.access.hasAccess({
      module: 'leave',
      entity: 'review',
      action: 'reject'
    });
  }

  private loadLeaves() {

    const self = this;
    const query = new Map<string, any>();
    query.set('leave_status', STATUS.APPLIED);

    this.loading = true;

    this.leaveService.getReviewLeaves(query).then(leaves => {

      self.loading = false;
      self.leaves = leaves;
    });
  }

  approve(leave_id) {

    this.approving[leave_id] = true;
    this.leaveService.approve(leave_id).then(response => {

      this.reviewed[leave_id] = true;
      this.approving[leave_id] = false;
    });
  }

  reject(leave_id) {

    this.rejecting[leave_id] = true;
    this.leaveService.reject(leave_id).then(response => {

      this.reviewed[leave_id] = true;
      this.rejecting[leave_id] = false
    });
  }
}
